.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  text-align: center;
}

#text {
  font-size: 24px;
  margin-bottom: 20px;
}

#author {
  font-size: 18px;
  margin-bottom: 40px;
}

.buttons {
  display: flex;
  justify-content: center;
}

#new-quote {
  padding: 10px 20px;
  font-size: 16px;
  margin-right: 10px;
  cursor: pointer;
}

#tweet-quote {
  padding: 10px 20px;
  font-size: 16px;
  background-color: #1da1f2;
  color: #fff;
  text-decoration: none;
  border-radius: 4px;
  cursor: pointer;
}

#tweet-quote:hover {
  background-color: #0c85d0;
}
